<template>
   <div>
    <el-table
    :data="items"
    height="600px"
    border
    style="width: 100%">
    <el-table-column
      prop="name"
      label="姓名"
      width="180">
    </el-table-column>
    <el-table-column
      prop="IdNumber"
      label="账号"
      width="180">
    </el-table-column>
    <el-table-column
      prop="PassWord"
      label="密码"
      width="180">
    </el-table-column>
    <el-table-column
      prop="email"
      label="邮箱"
      width="180">
    </el-table-column>
    <el-table-column label="操作">
      <template slot-scope="scope">
        <el-button
          size="mini"
          @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
        <el-button
          size="mini"
          type="danger"
          @click="handleDelete(scope.$index, scope.row)">删除</el-button>
      </template>
    </el-table-column>
  </el-table>
  <el-dialog
  title="修改用户信息"
  :visible.sync="open">
    姓名：<el-input v-model="item.name"></el-input>
    账号：<el-input :disabled="true" v-model="item.IdNumber"></el-input>
    密码：<el-input v-model="item.PassWord"></el-input>
    邮箱：<el-input :disabled="true" v-model="item.email"></el-input>
    <el-button @click="enter_edit">确认</el-button>
    <el-button @click="cancle_edit">取消</el-button>
  </el-dialog>
   </div>
    
  </template>
  
  <script>
import axios from 'axios';

    export default {
      data() {
        return {
            items:[],
            open:false,
            item:[]
        }
      },
      created() {
        axios.get('http://localhost:3000/NormalUsers').then(resp => {
            this.items = resp.data
        })
      },
      methods: {
        handleEdit(index,item){
            this.open = true
            // console.log(item);
            this.item = item
        },
        handleDelete(index,item){
            this.$confirm('此操作将永久删除该用户, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
            }).then(() => {
                axios.delete('http://localhost:3000/NormalUsers/'+item.id).then(resp => {
                console.log(resp);
                if(resp.statusText == "OK"){
                    this.$message({
                        message: '删除成功',
                        type: 'success'
                    });
                    location.reload()
                }else{
                    this.$message.error("删除失败，飞出宇宙了")
                }
            }
            )
            })
           
        },
        cancle_edit(){
            this.open = false
        },
        enter_edit(){
            var data = {
                name:this.item.name,
                IdNumber : this.item.IdNumber,
                PassWord: this.item.PassWord,
                email : this.item.email
            }
            axios.patch('http://localhost:3000/NormalUsers/'+this.item.id,data).then(resp => {
                if(resp.statusText == "OK"){
                    this.$message({
                        message: '修改成功',
                        type: 'success'
                    });
                    this.open = false
                    // location.reload()
                }else{
                    this.$message.error("修改失败，飞出宇宙了")
                }
            })
            
        }
      },
    }
  </script>