<template>
    <div>
        <el-input type="phone"></el-input>
        <button @click="send">发送</button>
        <button @click="Test">随机字符串</button>
        <button :style="style" @click="sendYzm"> {{ time }} </button>
    </div>
</template>

<script src="">


import axios from 'axios'
export default{
    data() {
        return {
            time:'获取验证码',
            style:''
        }
    },
    methods: {
        sendYzm(){
            this.style = 'pointer-events: none;'
            var times = 180
            setInterval(() => {
                
                if(times <= 0){
                    this.style = ''
                    this.time = '获取验证码'
                }else{
                    times -= 1
                    this.time = times
                }
            },1000)
        },
        send(){
            console.log(1111);
            // https://api.guaqb.cn/music/yx/qq.php?user=1224973233@qq.com&password=llpscxuqswrfhgcg&email=1224973233@qq.com&bt=hhhhh&nr=验证码：1234
            axios.get('/api/music/yx/qq.php?user=1224973233@qq.com&password=llpscxuqswrfhgcg&email=1224973233@qq.com&bt=hhhhh&nr=验证码：1234').then(resp => {
                console.log(resp);
            })
        },
        Test(){
            //生成一个随机字符串 5个长度 全以小写字母命名
            console.log(11111);
            let name = this.crateRandomString(5);
            //获取其它字符串格式，请自行改来用
            //...
            console.log(name);

        },
        createRandomChar(c='a-z'){
            switch(c){
                    case 'A-Z':
                        return String.fromCharCode(Math.trunc(Math.random()*25)+65);
                    case 'a-z':
                        return String.fromCharCode(Math.trunc(Math.random()*25)+97);
                    case '0-9':
                    default:
                        return String.fromCharCode(Math.trunc(Math.random()*10)+48);
                }
        },
        crateRandomString(length=11){
            let str='';
            for(let i=0; i<length; i++) str+=this.createRandomChar();
            return str;
        }
        
    },
    created() {
        // // 引入cors
        // const cors = require('cors')
        // // 使用 cors()
        // use(cors()) 
    },
}


</script>

