          this.loading = false
        }, 2000)
      },
      Quit(){
        var data = {
          statu : false,
          name : ''
        }
        axios.patch('http://localhost:3000/Status/1',data).then(resp => {
          console.log(resp);
          this.$route.params.name == null
          location.reload()
        })
      }
    },
    computed: {
      noMore () {
        return this.count >= 20
      },
      disabled () {
        return this.loading || this.noMore
      }
    },
    updated() {
        new Swiper ('.swiper-container', {
