<template>
    <!-- 注册帐号 -->
    <div>
        姓名 ： <b v-show="xm_boo" style="color: red;">姓名不能为空</b>
        <el-input type="text" v-model="name" placeholder="请输入您的名字：" @blur="xm_judg"></el-input>
        账号 ： <b v-show="zh_boo" style="color: red;">账号不能为空</b>
        <el-input type="text"  v-model="idnumber" placeholder="请输入注册的账号：" @blur="zh_judg"></el-input>
        密码 ： <b v-show="pass_boo" style="color: red;">密码不能为空</b>
        <el-input type="password"  v-model="pass" placeholder="请输入注册的密码：" @blur="pass_judg"></el-input>
        确认密码 ： <b v-show="enter_pass_boo" style="color: red;">此项不能为空</b>
        <el-input type="password"  v-model="enter_pass" placeholder="请确认密码 ：" @blur="pass_judg"></el-input>
        邮箱 ： <b v-show="email_boo" style="color: red;">此项不能为空</b>
        <el-input type="email"  v-model="email_pass" placeholder="请输入邮箱 ：" @blur="email_judg"></el-input>
        验证码 ： <b v-show="yzm_boo" style="color: red;">验证码不能为空</b>
        <el-input type="text"  v-model="yzm" placeholder="请输入五位数验证码：" @blur="yzm_judg"></el-input>
        <el-button @click="sendmsg"> {{ time }} </el-button>
        <el-button @click="Zhuce">注册</el-button>
        <el-button>取消</el-button>
    </div>
</template>

<script>
import axios from 'axios'

export default{
    data() {
        return {
            email_pass:'',
            email_boo : false,
            yzm_boo:false,
            yzm:'',
            yzm_enter:'',
            xm_boo:false,
            zh_boo:false,
            pass_boo:false,
            enter_pass_boo:false,
            name:'',
            idnumber:'',
            pass:'',
            enter_pass:'',
            items:[],
            SupperUsers:[],
            time:'发送验证码',
        }
    },
    methods: {
        sendmsg(){
             //生成一个随机字符串 5个长度 全以小写字母命名
            // console.log(11111);
            let name = this.crateRandomString(5);
            this.yzm_enter = name
            //获取其它字符串格式，请自行改来用
            //...
            // console.log(name);
            if(this.email_pass != '' && this.email_pass != null){
                axios.get('/api/music/yx/qq.php?user=1224973233@qq.com&password=llpscxuqswrfhgcg&email='+this.email_pass+'&bt=hhhhh&nr=验证码：>>'+name+">>验证码将在3min内有效").then(resp => {
                    console.log(resp);
                    this.$message({
                        message: '发送成功，请查看邮箱，3分钟内有效',
                        type: 'success'
                    });
                })

                this.style = 'pointer-events: none;'
                var times = 180
                setInterval(() => {
                    
                    if(times <= 0){
                        this.style = ''
                        this.time = '获取验证码'
                        name = ''
                    }else{
                        times -= 1
                        this.time = times
                    }
                },1000)
            }else{
                this.$message('邮箱不能为空');
            }

           
        },
        xm_judg(){
            if(this.name == ''){
                this.xm_boo = true
            }else{
                this.xm_boo = false
            }
        },
        zh_judg(){
            if(this.idnumber == ''){
                this.zh_boo = true
            }else{
                this.zh_boo = false
                this.items.forEach(i => {
                    if(i.IdNumber == this.idnumber){
                        this.$message({
                            message: '此账号已被注册，请更换',
                            type: 'warning'
                        });
                        this.idnumber = ''
                    }
                 })
            }
        },
        pass_judg(){
            if(this.pass == ''){
                this.pass_boo = true
            }else{
                this.pass_boo = false
            }

        },
        enter_pass_judg(){
            if(this.enter_pass == ''){
                this.enter_pass_boo = true
            }else if(this.pass != this.enter_pass){
                this.$message.error("两次密码不一致")
                this.enter_pass = ''
            }else{
                this.enter_pass_boo = false
            }

        },
        yzm_judg(){
            if(this.yzm == ''){
                this.yzm_boo = true
            }else{
                this.yzm_boo = false
            }
        },
        email_judg(){
            if(this.email_pass == ''){
                this.email_boo = true
            }else{
                this.email_boo = false
            }
        },
        Zhuce(){//注册
            var props = {
                name : this.name,
                IdNumber : this.idnumber,
                PassWord:this.pass,
                email:this.email_pass
            }

            if(this.name == '' || this.idnumber == '' || this.pass == '' || this.enter_pass == ''){
                this.$message.error("注册格式不正确")
            }else if(this.yzm == '' && this.yzm != this.yzm_enter){
                this.$message.error("验证码错误")
                this.yzm = ''
            }else if(this.yzm != '' && this.yzm == this.yzm_enter){
                axios.post('http://localhost:3000/NormalUsers',props).then(resp => {
                console.log(resp.statusText);
                if(resp.statusText == 'Created'){
                    this.$message({
                        message: '恭喜你，注册成功',
                        type: 'success'
                    });
                    this.name = ''
                    this.idnumber = ''
                    this.pass = '',
                    this.enter_pass  = ''
                    this.$router.push('/Login')
                }
               
            })
            }

        },
        createRandomChar(c='a-z'){
            switch(c){
                    case 'A-Z':
                        return String.fromCharCode(Math.trunc(Math.random()*25)+65);
                    case 'a-z':
                        return String.fromCharCode(Math.trunc(Math.random()*25)+97);
                    case '0-9':
                    default:
                        return String.fromCharCode(Math.trunc(Math.random()*10)+48);
                }
        },
        crateRandomString(length=11){
            let str='';
            for(let i=0; i<length; i++) str+=this.createRandomChar();
            return str;
        }
    },
    created() {
        axios.get('http://localhost:3000/Supperusers').then(resp => {
            this.SupperUsers = resp.data
        })
        axios.get('http://localhost:3000/NormalUsers').then(resp => {
            this.items = resp.data
        })
        this.SupperUsers.forEach(i => {
            this.items.push(i)
        });
    },
}

</script>


