<template>
    <div>
        <div style="display: flex;">
            <img src="https://misc.360buyimg.com/lib/img/e/logo-201305-b.png" alt="">
            <b style="font-size: xx-large;margin-top: 2%;margin-left: 2%;">欢迎登陆</b>
        </div>
        <div class="box_all">
            <div class="box">
                <div style="text-align: center;display: flex;">
                    <a href="" @click.prevent="SaoMa" style="text-decoration: none;margin-left: 30%">扫码登录</a>
                    <a href="" @click.prevent="Zhanghao" style="text-decoration: none;margin-left: 5%">账号登录</a>
                </div>
                <div v-if="box_boo">
                    账号 ：<b v-show="num_boo" style="color: red;">账号不能为空</b>
                    <el-input v-model="number" type="text" placeholder="请输入账号" @blur="NumNoNull"></el-input>
                    密码 ：<b v-show="pass_boo" style="color: red;">密码不能为空</b>
                    <el-input v-model="password" type="password" placeholder="请输入密码" @blur="PassNoNull"></el-input>
                    <el-button @click="enter_limit">确认</el-button>
                    <el-button @click="cancle">取消</el-button>
                    <router-link style="margin-left: 20%;" to="/Sign">注册</router-link>
                </div>
                <div v-else>
                    <img style="width: 100%;" src="../images/gzh.jpg" alt="">
                    <el-input type="text" v-model="YZM" placeholder="请输入公众号验证码"></el-input>
                    <b>关注公众号，后台发送“验证码”获取验证码</b>
                    <el-button @click="enter_gzh_admin">确认</el-button>
                </div>
            </div>
        </div>
        

    </div>
</template>

<script>
import axios from 'axios'

export default{
    data() {
        return {
            box_boo:true,
            number:'',
            password:'',
            num_boo:false,
            pass_boo:false,
            SupperUsers:[],
            NomalUsers:[],
            name:'',
            YZM:''
        }
    },
    methods: {
        enter_gzh_admin(){
            if(this.YZM == 'ywj666'){
                var props = {
                        statu : true,
                        name : '游客'
                    }
                    alert("登陆成功")
                axios.patch('http://localhost:3000/Status/1',props).then(resp => {
                console.log(resp);
                })
                this.$router.push({
                    name :'Index',
                    params : {
                        name : '游客'
                    }
                })
                           
            }else{
                alert("验证码错误")
                this.YZM = ''
            }
        },
        NumNoNull(){
            if(this.number == ''){
                this.num_boo = true
            }else{
                this.num_boo = false
            }
        },
        PassNoNull(){
            if(this.password == ''){
                this.pass_boo = true
            }else{
                this.pass_boo = false
            }
        },
        enter_limit(){
            var boo = true
            axios.get('http://localhost:3000/Status').then(resp => {
                if(resp.data[0].statu == ''){
                    for(var i = 0;i < this.SupperUsers.length;i++){
                    if(String(this.SupperUsers[i].IdNumber) == this.number){
                        if(String(this.SupperUsers[i].PassWord) == this.password){
                            var props = {
                                statu : true,
                                name : this.SupperUsers[i].name
                            }
                            alert("登陆成功")
                            boo = false
                            axios.patch('http://localhost:3000/Status/1',props).then(resp => {
                                console.log(resp);
                            })
                            this.$router.push('/SupperMe')
                            break;
                        }else{
                            this.$message.error("密码错误")
                            this.password = ''
                            break;
                        }
                    }else{
                        break;
                    }
                };
                var tempLists = []
                for(var j = 0;j < this.NomalUsers.length;j++){
                    if(String(this.NomalUsers[j].IdNumber) == this.number || this.NomalUsers[j].email == String(this.number)){
                        tempLists.push(this.NomalUsers[j])
                    }
                };
                if(tempLists.length > 0){
                    if(tempLists[0].PassWord == this.password){
                        var props = {
                                statu : true,
                                name : tempLists[0].name
                        }
                        alert("登陆成功")
                        axios.patch('http://localhost:3000/Status/1',props).then(resp => {
                                console.log(resp);
                        })
                        this.$router.push({
                            name :'Index',
                            params : {
                                name : tempLists[0].IdNumber
                            }
                        })
                        
                    }else{
                        this.$message.error("密码错误")
                        this.password = ''
                    }
                }else{
                    if(boo){
                        this.$message.error("账号不存在")
                    }else{
                        
                    }
                    
                }
                }
            })
               
        },
        cancle(){
            this.$router.go(-1)
        },
        SaoMa(){
            this.box_boo = false
        },
        Zhanghao(){
            this.box_boo = true
        }
    },
    created() {
        var props = {
          statu : false,
          name : ''
        }
        axios.patch('http://localhost:3000/Status/1',props).then(resp => {
            console.log(resp);
        })
        axios.get('http://localhost:3000/Supperusers').then(resp => {
            this.SupperUsers = resp.data
        })
        axios.get('http://localhost:3000/NormalUsers').then(resp => {
            this.NomalUsers = resp.data
            // console.log(this.NomalUsers[0].IdNumber);
        })
    },
}

</script>

<style scoped src="../css/css2.css">

</style>