//1. 导入 Vue 和 VueRouter 的包
import Vue from 'vue'
import VueRouter from 'vue-router'
import Index from '../components/index.vue'
import Page404 from '../404/Page404'
import Login from '../components/Login'
import Sign from '../components/Sign'
import ShopLabs from '../components/ShopLabs'
import HomeImage from '../components/HomeImage'
import Shops from '../components/Shops'
import networkError from '../components/networkError'
import TestEmail from '../components/TestEmail'
import SupperMe from '../components/SupperMe'
import Adminner from '../components/Adminner'

//2. 调用Vue.use()函数，把VueRouter安装为Vue的插件
Vue.use(VueRouter)
 
//3. 创建路由的实例对象
const router = new VueRouter({
    routes: [
        {
            path : '/',
            name : 'Index',
            component : Index
        },
        {
            path: '/404',
            name:'Page404',
            component: Page404
        }, 
        {
            path: '/networkError',
            name:'networkError',
            component: networkError
        }, 
        {
            path: '/Login',
            name:'Login',
            component: Login
        }, 
        {
            path: '/Sign',
            name:'Sign',
            component: Sign
        }, 
        {
            path: '/ShopLabs',
            name:'ShopLabs',
            component: ShopLabs
        }, 
        {
            path: '/HomeImage',
            name:'HomeImage',
            component: HomeImage
        }, 
        {
            path: '/Shops',
            name:'Shops',
            component: Shops
        }, 
        {
            path: '/TestEmail',
            name:'TestEmail',
            component: TestEmail
        }, 
        {
            path: '/SupperMe',
            name:'SupperMe',
            component: SupperMe
        }
        , 
        {
            path: '/Adminner',
            name:'Adminner',
            component: Adminner
        }
    ]
})
 
//4. 向外共享路由的实例对象
export default router